import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import {SearchService} from '../services/search.service';
import { Solicitation } from '../models/solicitation.model';

@Component({
  selector: 'app-search-form',
  templateUrl: './search-form.component.html',
  styleUrls: ['./search-form.component.scss']
})
export class SearchFormComponent implements OnInit {

  private provisoryHour = ' 03:00:00';
  protected model = new Solicitation;
  @Output() searchResult = new EventEmitter<Solicitation>();

  constructor(private  searchService: SearchService) { }

  ngOnInit() {
  }

  search() {
    if (this.model.travelDate) {
      if (this.model.travelDate.length < 11) {
        this.model.travelDate += this.provisoryHour;
      }
    }
    if (this.model.returnDate) {
      if (this.model.returnDate.length < 11) {
        this.model.returnDate += this.provisoryHour;
      }
    }
    this.searchService.searchSolicitation(this.model).subscribe((data) => {
        this.searchResult.emit(data.data);
      }, error => {
        console.error(error);
      }
    );
  }

}



